.main {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .btn {
    width: 1rem;
    height: 0.4rem;
    text-align: center;
    line-height: 0.4rem;
    border: 1px solid #ccc;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    z-index: 1;

    &:hover {
      background: rgba(67, 153, 146, 0.294);
    }
  }

  .box {
    width: 2rem;
    height: 2rem;
    border: 1px solid #333;
    border-radius: 50%;
    position: relative;
    
    > div {
      width: 0.1rem;
      height: 0.1rem;
      background: red;
      border-radius: 50%;
      position: absolute;
    }
  }
}
