.main {
    width: 100%;
    height: 100%;
    background: #000;
    color: #fff;
    padding: 0.2rem;
    font-size: 0.14rem;
    overflow: auto;

    .tit {
        border-bottom: 1px solid #e1e1e1;
        padding-bottom: 0.2rem;
        > div {
            &:first-child {
                font-size: 0.4rem;
            }
            &:last-child {
                font-size: 0.3rem;

                > span {
                    padding-right: 0.1rem;
                }
            }
        }
    }

    .head {
        display: flex;
        margin: 0.2rem 0;
        justify-content: space-between;

        > div {
            &:first-child {

            }
            &:last-child {
                display: flex;

                > div {
                    width: 0.3rem;
                    height: 0.2rem;
                    text-align: center;
                    line-height: 0.2rem;
                    margin-left: 0.2rem;
                    cursor: pointer;
                }
            }
        }
    }

    .list {
        .week {
            display: flex;
            margin-bottom: 0.1rem;
    
            > div {
                flex: 0 0 14.28%;
                text-align: center;
                font-size: 0.14rem;
            }
        }

        .time {
            width: 100%;
            display: flex;
            flex-flow: row wrap;

            > div {
                flex: 0 0 14.28%;
                height: 0.4rem;
                margin-bottom: 0.1rem;
                font-size: 0.14rem;

                > div {
                    width: 100%;
                    height: 0.2rem;
                    line-height: 0.2rem;
                    text-align: center;
                }
            }

            .speical {
                color: #7e7575;
            }
        }
    }
}