.main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: row nowrap;

  .left {
    width: 0.35rem;
    height: 100%;
    background: rgb(85, 69, 69);

    > div {
      width: 0.35rem;
      height: 0.35rem;
      text-align: center;
      line-height: 0.35rem;
      cursor: pointer;
    }
  }
  .right {
    flex: 1;
    color: rgb(166, 134, 207);
    overflow: auto;

    > div {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 0.25rem;
      color: #000;
    }
  }
}
