.icomfontPage {
  width: 100%; // 默认宽度
  display: flex;
  flex-flow: row wrap;

  > div {
    width: 25%;
  }

  .left {
    position: relative;
    .box {
      background: red;
      overflow: hidden;
      transition: all 0.4s linear;
    }
  }

  .right {
    .san {
      border: 1rem solid #000;
      border-color: transparent transparent blue transparent;
      width: 0;
    }

    .dian {
      width: 1rem;
      height: 1rem;
      border: 1px solid #ccc;
      display: flex;
      justify-content: space-between;

      > div {
        width: 0.3rem;
        height: 0.3rem;
        border-radius: 50%;
        background: turquoise;

        &:nth-child(2) {
          align-self: end;
        }
      }
    }
  }

  .box {
    height: 3rem;
    position: relative;

    .traffic {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      height: 4rem;
      width: 2rem;
      background: #333;
      z-index: 10;

      .traffic::before {
        content: "";
        width: 1.5rem;
        height: 3rem;
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translateX(-50%);
        background: #eee;
        background: linear-gradient(top, rgba(240,240,240,0.9), #1acbfc 70%);
        z-index: 0;
      }

      .light {
        width: 0.7rem;
        height: 0.7rem;
        background: #000;
        border-top: 0.05rem solid #000;
        border-radius: 50%;
        margin: 0.5rem auto;
      }

      .red {
        animation: redAction 10s linear infinite;
      }
      .amber {
        animation: amberAction 10s linear infinite;
      }
      .green {
        animation: greenAction 10s linear infinite;
      }

      @keyframes redAction {
        0% {
          background: black;
        }
        2%, 40% {
          background-color: red;
        }
        42%, 100% {
          background: black;
        }
      }
      @keyframes amberAction {
        0%, 20% {
          background: black;
        }
        22%, 40% {
          background: #FF7E00;
        }
        42%, 80% {
          background: black;
        }
        82%, 100% {
          background: #FF7E00;
        }
      }
      @keyframes greenAction {
        0%, 40% {
          background: black;
        }
        42%, 80% {
          background: green;
        }
        82%, 100% {
          background: black;
        }
      }
    }
  }

  .cardBox {
    height: 4rem;
    position: relative;
    .card {
      width: 100%;
      height: 100%;
      background: #fff url(http://placekitten.com/350/450);
      background-size: cover;
      background-position: 0 0;
      box-shadow: 0 0 0.5rem rgba(0,0,0,0.4);
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
  
      .info {
        background: #fff;
        margin: 0;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        padding: 0.05rem 0.1rem;
        overflow: hidden;
        height: 0.3rem;
        transition: all 0.5s cubic-bezier(.48,-0.28,.41,1.4);
    
        p {
          opacity: 0;
          transform: translateY(2em);
          transition: all 0.4s ease-out;
        }
      }
  
      &:hover .info {
        height: 1.3rem;
    
        p {
          opacity: 1;
          transform: translateY(0);
        }
      }
    }
  }

  .btnBox {
    height: 3rem;
    background-color: #134;
    transition: background 0.5s ease-out;
    position: relative;

    &.on {
      background-color: #099;
    }
    button {
      background: transparent;
      border: 2px solid #fff;
      border-radius: 0.1rem;
      color: #fff;
      cursor: pointer;
      font-size: 0.24rem;
      padding: 0.1rem 0.2rem;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate3d(-50%, -50%, 0.1rem);
      transition: all 0.4s ease-out;

      &:hover, &.active {
        background: white;
        color: #099;
        outline: none;
      }
      &:focus {
        outline: none;
      }
    }
    > div {
      color: #fff;
      text-align: center;
      position: absolute;
      left: 50%;
      top: (50% + 0.4rem);
      transform: translate(-50%, -50%);
      transition: all 0.5s cubic-bezier(.83,-0.43,.21,1.42);
    }

    .hide {
      opacity: 0;
      top: calc(50% + 8em);
    }
    .show {
      opacity: 1;
      top: calc(50% + 6em);
    }
  }

  .principle {
    height: 3rem;
    position: relative;
    background: #e9b59f;

    .shape {
      background: #2d97db;
      border: 1em solid #fff;
      width: 4em;
      height: 4em;
      position: absolute;
      top: calc(50% - 2em);
      left: calc(50% - 2em);
    }

    .a {
      animation: sevenb 3s infinite linear;
      top: calc(50% - 2em);
      left: calc(50% - 9em);
      transform-origin: 10em 50%;
    }

    .b {
      animation: sevenb 6s infinite linear reverse;
      background-color: yellow;
      width: 2em;
      height: 2em;
      left: calc(50% - 1em);
      top: calc(50% - 1em);
    }
    
    @keyframes sevenb {
      100% {
        transform: rotateZ(360deg);
      }
    }
  }

  .justify {
    height: 3rem;
    background: radial-gradient(circle at center, #fff, #fff 50%, #aaa);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;

    .baymax {
      width: 1rem;
      height: 1rem;
      border-radius: 50%;
      background: linear-gradient(to bottom, #efefef, #efefef 50%, #000 50%, #000);
      background-position: 0 -100%;
      background-size: 200% 200%;
      animation: blink 6s infinite;
    }
    
    @keyframes blink {
      0%, 50% {
        background-position: 0 100%;
      }
      85%, 95% {
        background-position: 0 70%;
      }
      100% {
        background-position: 0 100%;
      }
    }
  }
  
  .btn {
    height: 3rem;
    background: gray;
    display: flex;
    align-items: center;
    justify-content: center;

    > div {
      width: 2rem;
      height: 0.8rem;
      text-align: center;
      line-height: 0.8rem;
      border-radius: 0.1rem;
      background: #e5ac8e;
      color: #fff;
      border: none;
      font-size: 0.25rem;
      position: relative;
      overflow: hidden;
      transition: transform 0.3s linear;

      &:hover {
        transform: scale(2);
      }

      &::after {
        content: '';
        width: 3rem;
        height: 1rem;
        position: absolute;
        left: -1.5rem;
        top: 0;
        background: linear-gradient(to bottom, rgba(229, 172, 142, 0), rgba(255,255,255,0.5) 50%, rgba(229, 172, 142, 0));
        transform: rotateZ(60deg) translate(-0.1rem, 0.75rem);
      }
      
      &:hover::after, &:focus::after {
        animation: sheen 1s forwards;
      }

      @keyframes sheen {
        100% {
          transform: rotateZ(60deg) translate(1rem, -2.5rem);
        }
      }
      
    }
  }

  .move {
    height: 2rem;
    background: #0bbea0;
    display: flex;
    align-items: center;
    padding: 0.2rem;

    > div {
      background: white;
      border-radius: 50%;
      color: #222;
      font-size: 0.24rem;
      font: Helvetica, Arial, Sans-serif;
      height: 0.5rem;
      width: 0.5rem;
      text-align: center;
      line-height: 0.5rem;
      animation: moveAct 2s ease-in-out infinite;

      &:hover {
        animation-play-state: paused;
      }
    }

    @keyframes moveAct {
      0% {
        transform: translateX(0);
      }
      50% {
        transform: translateX(3rem);
      }
      100% {
        transform: translateX(0);
      }
    }
  }

  .scroll {
    height: 2rem;
    overflow: auto;
    padding: 0 2%;

    > div {
      width: 98%;
      height: 0.4rem;
      text-align: center;
      line-height: 0.4rem;
      border: 1px solid #e1e1e1;
      margin-bottom: 0.05rem;
    }
  }

}
