.content {
  width: 100%;
  height: 100%;
  position: relative;

  .drag {
    > div {
      &:nth-child(1) {
        width: 1rem;
        height: 0.3rem;
        border: 1px solid #ccc;
      }
  
      &:nth-child(2) {
        width: 1rem;
        height: 1rem;
        border: 1px solid #ccc;
        position: absolute;
        left: 3rem;
        top: 3rem;
      }
    }
  
    :global(.draggable-style) {
      color: red;
    }
  }
}
