}
ol,
ul {
  list-style: none;
}
html,
body,
#root {
  width: 100%;
  height: 100%;
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
