}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 5px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
}
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: rgba(255, 255, 255, 0.43);
}
.reset-table table {
  width: 100%;
  table-layout: fixed !important;
}
.reset-table table td.truncated {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.reset-table table td.truncated > div {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.reset-table .ant-table-header {
  background-color: #e5ebfb;
}
.reset-table .ant-table-body,
.reset-table .ant-table-body-outer {
  height: calc(100% - 0.5rem);
  background: transparent;
}
.reset-table .ant-table-body .ant-table-body-inner,
.reset-table .ant-table-body-outer .ant-table-body-inner {
  background: #fff;
}
.reset-table .ant-table-thead > tr > th {
