  height: 0.5rem;
  line-height: 0.5rem;
  color: #333;
  outline: none;
  background-color: #e5ebfb;
  border-bottom: 0;
  text-align: left;
}
.reset-table .ant-table-thead > tr > th:first-of-type {
  padding: 0 0.2rem 0 0.4rem;
}
.reset-table .ant-table-thead > tr > th .ant-table-column-title {
  font-size: 0.16rem;
  font-weight: 600;
}
.reset-table .ant-table-thead > tr:first-child > th:first-child,
.reset-table .ant-table-thead > tr:first-child > th:last-child {
  border-radius: 0;
}
.reset-table .ant-table-tbody > tr:nth-of-type(2n) {
  background-color: #f0f4fd;
}
.reset-table .ant-table-tbody > tr:nth-of-type(2n + 1) {
  background-color: #fff;
}
.reset-table .ant-table-tbody > tr > td {
  padding: 0 0.2rem;
  line-height: 0.5rem;
  height: 0.5rem;
  font-size: 0.16rem;
  color: #333;
  background: transparent;
  border-radius: 0;
}
.reset-table .ant-table-tbody > tr > td:first-of-type {
  padding: 0 0.2rem 0 0.4rem;
}
.reset-table .ant-table-tbody > tr > td > div {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.reset-table .ant-table-tbody > tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td,
.reset-table .ant-table-thead > tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td,
.reset-table .ant-table-thead > tr.ant-table-row-hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td,
.reset-table .ant-table-tbody > tr.ant-table-row-hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td {
  background-color: #ebf0ff;
}
.auto-scroll-y {
  position: absolute;
  width: 100%;
  height: calc(100% - 0rem);
}
.auto-scroll-y .ant-spin-nested-loading,
.auto-scroll-y .ant-spin-container,
.auto-scroll-y .ant-table,
.auto-scroll-y .ant-table-container,
.auto-scroll-y .ant-table-scroll,
.auto-scroll-y .ant-table-body-inner,
.auto-scroll-y .ant-table-fixed-left,
.auto-scroll-y .ant-table-fixed-right,
.auto-scroll-y .ant-table-content {
  height: 100%;
}
.auto-scroll-y .ant-table-content {
  position: relative;
}
.auto-scroll-y .ant-table-body,
.auto-scroll-y .ant-table-body-inner {
  overflow-y: auto !important;
}
.auto-scroll-y .ant-table-body {
  height: calc(100% -0.5rem);
}
.auto-scroll-y .ant-table-body-outer {
  height: calc(100% -0.5rem);
}
.auto-scroll-y .ant-table-body-innder {
  overflow-x: hidden;
}
.auto-scroll-y .ant-table-placeholder {
  position: absolute;
  top: 0.5rem;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: none;
  border-top: none;
}
.reset-ant-pagination {
  display: flex;
  background-color: #f5f6fa;
}
.reset-ant-pagination .ant-pagination-total-text {
  color: #666;
  margin-right: 0.2rem;
  font-size: 0.14rem;
  height: 0.35rem;
  line-height: 0.35rem;
}
.reset-ant-pagination .ant-pagination-item-active:focus a,
.reset-ant-pagination .ant-pagination-item-active:hover a {
  color: #fff;
}
.reset-ant-pagination .ant-pagination-prev .ant-pagination-item-link,
.reset-ant-pagination .ant-pagination-next .ant-pagination-item-link {
  border-radius: 1px;
  border: 1px solid #e1e3ec;
}
.reset-ant-pagination .ant-pagination-prev .ant-pagination-item-link .anticon,
.reset-ant-pagination .ant-pagination-next .ant-pagination-item-link .anticon {
  color: #333;
}
.reset-ant-pagination .ant-pagination-prev .ant-pagination-item-link:hover,
.reset-ant-pagination .ant-pagination-next .ant-pagination-item-link:hover {
