  line-height: 0.3rem;
  border-radius: 1px;
  font-size: 0.14rem;
  border-color: #e1e3ec;
  margin-right: 0.05rem;
  min-width: 0.3rem;
  text-align: center;
}
.reset-ant-pagination .ant-pagination-prev:not(.ant-pagination-disable):hover .ant-pagination-item-link,
.reset-ant-pagination .ant-pagination-next:not(.ant-pagination-disable):hover .ant-pagination-item-link {
