  border-color: #427aff;
}
.reset-ant-pagination .ant-pagination-item-active {
  background: #427aff;
}
.reset-ant-pagination .ant-pagination-item-active a {
  font-size: 0.14rem;
  color: #fff;
}
.reset-ant-pagination .ant-pagination-item-active:hover a {
