}
.reset-ant-pagination .ant-pagination-options .ant-pagination-options-size-changer > div:first-child .ant-select-selection-item {
  height: 0.3rem;
  line-height: 0.3rem;
}
.reset-ant-pagination .ant-pagination-options .ant-pagination-options-size-changer > div:first-child .ant-select-arrow {
  right: 7%;
  transform: translateY(-45%);
