  color: #333;
}
.reset-ant-pagination .ant-pagination-options .ant-select-selection {
  height: 0.3rem;
  line-height: 0.3rem;
  border-radius: 1px;
  border: 0.01rem solid #e1e3ec;
}
.reset-ant-pagination .ant-pagination-options .ant-select-selection:hover {
  border-radius: #427aff;
  border-right-width: 1px !important;
}
.reset-ant-pagination .ant-pagination-options .ant-select-selection:focus {
  border-radius: #427aff;
  border-right-width: 1px !important;
  box-shadow: none;
}
.reset-ant-pagination .ant-pagination-options .ant-pagination-options-quick-jumper {
  height: 0.3rem;
  line-height: 0.3rem;
}
.reset-ant-pagination .ant-pagination-options .ant-pagination-options-quick-jumper > input {
  height: 0.3rem;
  line-height: 0.35rem;
  width: 0.52rem;
  border-radius: 1px;
  padding: 0.04rem 0.11rem;
  margin: 0 0.08rem;
  border-color: #e1e3ec;
  font-size: 0.14rem;
  color: #333;
  border: 0.01rem solid #e1e3ec;
}
.reset-ant-pagination .ant-pagination-options .ant-pagination-options-quick-jumper > input:hover {
  border-color: #427aff;
}
.reset-ant-pagination .ant-pagination-options .ant-pagination-options-quick-jumper > input:focus {
  box-shadow: none;
}
.reset-ant-pagination .ant-pagination-options .ant-select-selection-selected-value,
.reset-ant-pagination .ant-pagination-options .ant-pagination-options-quick-jumper {
  font-size: 0.14rem;
}
.reset-ant-pagination .ant-pagination-options .ant-select-dropdown {
  border-radius: 0.1rem;
  min-width: 0.9rem;
  background: #fff;
}
.reset-ant-pagination .ant-pagination-options .ant-select-dropdown .ant-select-dropdown-menu-item {
  font-size: 0.14rem;
  color: #333;
  height: 0.3rem;
  line-height: 0.3rem;
  padding: 0;
  padding-left: 0.1rem;
  text-align: left;
}
.reset-ant-pagination .ant-pagination-options .ant-select-dropdown .ant-select-dropdown-menu-item:first-child {
