.content {
    width: 100%;
    height: 100%;
    margin: 0.2rem 0;
    overflow: hidden;
    // position: relative;
  
    .main {
      width: 100%;
    //   position: absolute;
    //   left: 0;
    //   top: 0;
      overflow: auto;
  
      .tHead {
        width: 100%;
  
        > div {
          width: 100%;
          display: flex;
          flex-flow: row nowrap;
          height: 0.3rem;
          line-height: 0.3rem;
  
          > div {
            flex: 1;
            border-bottom: 1px solid #e1e1e1;
          }
  
          .name {
            min-width: 1rem;
            padding-left: 0.2rem;
          }
  
          .tol {
            min-width: 0.6rem;
          }
    
          .td {
            min-width: 0.5rem;
            text-align: center;
            border-left: 1px solid #e1e1e1;
          }
  
          .week {
            border-right: 1px solid #999;
          }
        }
      }
  
      .tBody {
        > div {
          width: 100%;
          display: flex;
          flex-flow: row nowrap;
          height: 0.3rem;
          line-height: 0.3rem;
  
          > div {
            flex: 1;
            border-bottom: 1px solid #e1e1e1;
          }
  
          .name {
            min-width: 1rem;
            padding-left: 0.2rem;
          }
  
          .tol {
            min-width: 0.6rem;
          }
  
          .td {
            min-width: 0.5rem;
            text-align: center;
            border-left: 1px solid #e1e1e1;
          }
  
          .select {
            cursor: pointer;
  
            &:hover {
              color: #427aff;
            }
          }
  
          .weekBg {
            background: #f1f1f1;
          }
  
          .week {
            border-right: 1px solid #999;
          }
        }
      }
  
      .total {
        width: 100%;
        display: flex;
        flex-flow: row nowrap;
  
        > div {
          height: 0.3rem;
          line-height: 0.3rem;
          color: #999;
          flex: 1;
          border-bottom: 1px solid #e1e1e1;
        }
  
        .name {
          min-width: 1rem;
          padding-left: 0.2rem;
        }
  
        .tol {
          min-width: 0.6rem;
          word-break: keep-all;
          position: relative;
  
          > span {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
          }
        }
  
        .td {
          min-width: 0.5rem;
          text-align: center;
        }
  
        .week {
          border-right: 1px solid #999;
        }
      }
    }
}
  