.page-base-js {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: row nowrap;

  > div {
    &:nth-child(1) {
      width: 1rem;
      background: #fff;

      > div {
        margin-top: 0.3rem;

        &:hover {
          color: #427aff;
        }
      }

      .select {
        color: #427aff;
      }
    }
    &:nth-child(2) {
      width: 100%;
      height: 100%;
      overflow: hidden;
    }
  }
}