.main {
  width: 100%;
  height: 100%;
  position: relative;

  .box {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: baseline;
    position: relative;
    overflow: auto;

    > div {
      width: 1rem;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
    }
  }

  .btn {
    position: absolute;
    right: 0;
    bottom: 0;
    word-break: keep-all;
    padding: 0.1rem;
    background: rgb(124, 152, 163);
  }
}
