.main {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;

  .left,
  .right {
    width: 0.5rem;
    height: 0.5rem;
    text-align: center;
    line-height: 0.5rem;
    position: absolute;
    top: 50%;
    z-index: 1;
    transform: translateY(-50%);
    cursor: pointer;
    i {
      font-size: 0.4rem;
    }

    &:hover {
      background: rgb(236, 228, 228);
    }
  }

  .left {
    left: 0.1rem;
  }

  .right {
    right: 0.1rem;
  }

  .dots {
    width: 1rem;
    display: flex;
    justify-content: space-between;
    position: absolute;
    left: 50%;
    bottom: 0.3rem;
    z-index: 1;
    transform: translateX(-50%);

    > div {
      width: 0.1rem;
      height: 0.1rem;
      border-radius: 50%;
      background: #fff;
    }
  }

  .imgs {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: row nowrap;
    position: absolute;
    left: 0;
    top: 0;

    > div {
      flex: 0 0 100%;
      font-size: 0.4rem;
      display: flex;
      align-items: center;
      justify-content: center;

      &:nth-child(1) {
        background: rgb(124, 157, 167);
      }
      &:nth-child(2) {
        background: rgb(212, 163, 145);
      }
      &:nth-child(3) {
        background: rgb(161, 117, 159);
      }
    }
  }
}