.main {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  .clockFace {
    width: 2.4rem;
    height: 2.4rem;
    border: 0.5px solid #333;
    border-radius: 50%;
    position: relative;

    .scale {
      width: 100%;
      height: 100%;
      position: relative;

      > div {
        width: 0.02rem;
        background: #333;
        position: absolute;
        top: 0;
        left: 50%;
        transform-origin: center 1.2rem;

        > span {
          position: relative;
          top: 0.15rem;
          left: -0.03rem;
        }
      }
    }

    .hand {
      position: absolute;
      left: 50%;
      top: 50%;
      background: #333;
      transform-origin: center bottom;
    }

    :global(.hourHand) {
      width: 0.06rem;
      height: 0.5rem;
      margin: -0.5rem 0 0 -0.025rem;
    }

    :global(.minHand) {
      width: 0.04rem;
      height: 0.7rem;
      margin: -0.7rem 0 0 -0.015rem;
    }

    :global(.secondHand) {
      width: 0.02rem;
      height: 1rem;
      margin: -1rem 0 0 0;
    }

    .bail {
      position: absolute;
      left: 50%;
      top: 50%;
      width: 0.15rem;
      height: 0.15rem;
      border-radius: 50%;
      background: blue;
      margin: -0.06rem;
    }
  }
}