.main {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;

  .box {
    width: 1rem;
    height: 1rem;
    background: rgb(141, 197, 201);
    text-align: center;
    line-height: 1rem;
    position: absolute;
    left: 0;
    top: 0;
  }
}
